import React from 'react'
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'
import styled from 'styled-components'

// const ContributionMap = styled(MapContainer)`
//   height: 50vh;
// `

const ContributionMarker = ({ attributes, actionTranslations }) => {
  const [name, street, city, actions, lat, lng] = attributes
  // const icon = actionIcons[actions]
  // console.log(name, icon)
  // const iconImage = new Icon({
  //   iconUrl: icon.url,
  //   iconSize: [32, 32],
  // })
  return (
    <Marker position={[lat, lng]}>
      <Popup>
        <p>
          {name}<br />
          {street}<br />
          {city}<br />
        </p>
        {actionTranslations[actions]}
      </Popup>
    </Marker>
  )
}

export default ({ locations, actionTranslations }) => {
  if (typeof window !== 'undefined') {
    return (
      <MapContainer center={[48.39667380182477,9.99045219366275]} zoom={13} scrollWheelZoom={false}>
        <TileLayer
          attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        />
        {
          locations.map((attributes, index) => (
            <ContributionMarker key={index} attributes={attributes} actionTranslations={actionTranslations} />
          ))
        }
      </MapContainer>
    )
  }
}
